import React, { createRef, useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react'
import { useWindowSize } from 'react-use'
import { get } from 'lodash'
import { IoTriangle } from 'react-icons/io5'
import gsap from 'gsap'
import ReactFullpage from '@fullpage/react-fullpage'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Heading from '../../components/Heading'
import FullpageHeight from '../../components/FullpageHeight'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Image from '../../components/Image'
import BackgroundImage from '../../components/BackgroundImage'
import theme, { responsive } from '../../components/ThemeProvider/theme';

import useResponsive from '../../contexts/mediaQuery/useResponsive'

import Grid from '../CataloguePage/Grid'
import useData from '../TrashPage/data/useData'
import useShowHeader from '../../contexts/header/useShowHeader';
import containerWidthContext from '../../contexts/containerWidth/context';

import Face from '../Face'
import mountTop from './mount-top.png'
import mountMiddle from './mount-middle.png'
import mountBottom from './mount-bottom.png'
// import trashesHighlight from './highlight-trashes.png'
import title from './title.svg'
import titleOverlay from './title-overlay.svg'
import bubble1 from './bubble-1.svg'
import bubble2 from './bubble-2.svg'
import bubble3 from './bubble-3.svg'
import bubble4 from './bubble-4.svg'
import bubble5 from './bubble-5.svg'

import trash1 from './trash-1.png'
import trash2 from './trash-2.png'
import trash3 from './trash-3.png'
import trash4 from './trash-4.png'

// const TriangleDown = props => <IoTriangle style={{ transform: 'rotate(180deg)' }} {...props} />

const mountRatio = 3424 / 1843
const titleRatio = 1920 / 430
const scrollingDuration = 1

let timeline
let timeline2

const HomePage = ({ history }) => {
  useShowHeader()
  const { isMobile } = useResponsive()
  const heroTrashRef = useRef()
  const bubbleRef = useRef()
  const trashMountRef = useRef()
  const data = useData()
  const { containerWidth } = useContext(containerWidthContext)
  const windowSize = useWindowSize()
  // const [currentPage, setCurrentPage] = useState(0)
  const [trashMx, trashMt, trashWidth] = useMemo(() => {
    const scaleRatio = Math.min(isMobile ? 6 : 1.375, 3000 / windowSize.width)
    const titleClearance = containerWidth / titleRatio * (isMobile ? 1.5 : 1.1) + 60
    const mountTopHeight = containerWidth / mountRatio * scaleRatio * (100 / 1843)
    return [
      (1 - scaleRatio) * windowSize.width / 2,
      Math.max(titleClearance - mountTopHeight, 0),
      scaleRatio * windowSize.width,
    ]
  }, [windowSize, isMobile, containerWidth])

  const pages = useMemo(() => [
    <Box pt={theme.headerHeight} height="100%">
      <Container pt={responsive('10%', '5%')} textAlign="center" height="100%" position="relative" px="0">
        <Box height="100%" mx={responsive('-7.5%', 0)}>
          <Box.Relative height="100%">
            <Heading.H2 fontWeight="900" letterSpacing="0.125em" textAlign="right" px="10.5%" fontSize={responsive('3em', '2.5em')}>史上最難丟的101種垃圾</Heading.H2>
            <BackgroundImage src={title} ratio={titleRatio}>
              <Box.FullAbs zIndex={2}>
                <BackgroundImage src={titleOverlay} ratio={titleRatio} />
                <Text.H1 display="none">回收大百科</Text.H1>
              </Box.FullAbs>
            </BackgroundImage>
            <Box.Absolute left="5%" top="2%" width="30%" transform="translate3d(0, -15%, 0)">
              <BackgroundImage src={get(data, [1, 'img'])} ratio={850 / 624} transform="rotate(7deg)">
                <Face id="1" />
              </BackgroundImage>
            </Box.Absolute>
            {/* <Box.Absolute bottom="4em" left="0" right="0" textAlign="center">
              <ScrollButton onClick={() => fpApi.moveSectionDown()}>往下滑走進垃圾堆 聽聽垃圾的心聲</ScrollButton>
            </Box.Absolute> */}
          </Box.Relative>
        </Box>
      </Container>
    </Box>,
    <>

    </>,
    <>

    </>,
    <>
      <Container textAlign="center" pt={responsive('75%', '25%')} color="black">
        <Heading
          letterSpacing="0.125em"
          fontWeight="900"
          fontSize={responsive('5em', '3em')}
          whiteSpace="pre-wrap"
          lineHeight="1.75"
        >「每個迷路的垃圾，{isMobile ? '\n' : ''}都有個丟錯的主人」
        </Heading>
        <Text lineHeight="3em" fontSize={responsive('3.5em', '1.75em')} letterSpacing="0.125em">以下是台灣人最常丟錯的101件垃圾</Text>
      </Container>
    </>,
    <>

    </>,
  ], [data])
  const pageRefs = useMemo(() => pages.map(() => createRef()), [pages])
  useEffect(() => {
    if (timeline) {
      timeline.kill()
      timeline2.kill()
    }
    timeline = gsap.timeline({ defaults: { duration: scrollingDuration } })
    timeline2 = gsap.timeline()
    gsap.set(heroTrashRef.current, {
      right: trashWidth * 0.66,
    })
    timeline.to(heroTrashRef.current, {
      width: trashWidth * 0.25,
      right: trashWidth * (isMobile ? -0.08 : 0.22),
      top: windowSize.height * (isMobile ? 0.2 : 0.23),
      duration: 0.8 * scrollingDuration,
    }, 0.2 * scrollingDuration)
    timeline.to(heroTrashRef.current.querySelector('.trash'), {
      rotate: isMobile ? 368 : 385,
      duration: 0.8 * scrollingDuration,
    }, 0.2 * scrollingDuration)
    timeline.to(bubbleRef.current, {
      opacity: 1,
      duration: 0.2 * scrollingDuration,
    }, 0.8 * scrollingDuration)
    timeline.to(trashMountRef.current, {
      scale: isMobile ? 1.25 : 1.5,
      x: trashWidth * (isMobile ? 0.2 : 0.25),
      y: windowSize.height * (isMobile ? 0.2 : 0.25),
    }, 0)
    timeline.to(trashMountRef.current.querySelector('.margin-adj'), {
      marginTop: 0,
    }, 0)

    timeline.to(trashMountRef.current, {
      y: `-=${windowSize.height * (isMobile ? 0.75 : 0.5)}`,
    }, scrollingDuration)
    timeline.to(heroTrashRef.current, {
      opacity: 0.3,
      top: `-=${windowSize.height * (isMobile ? 0.75 : 0.5)}`,
    }, scrollingDuration)
    timeline.to(trashMountRef.current.querySelector('.trash-mount'), {
      opacity: 0.3,
    }, scrollingDuration)
    timeline2.to(trashMountRef.current.querySelectorAll('.trash-bubble'), {
      stagger: 0.25,
      scale: 1,
      ease: 'back.out(1.7)',
      duration: scrollingDuration * 0.5,
    }, 0.5)
    timeline2.to(trashMountRef.current.querySelectorAll('.trash-bubble'), {
      opacity: 1,
      stagger: 0.25,
      duration: scrollingDuration * 0.5,
    }, 0.5)

    // timeline.to(trashMountRef.current, {
    //   y: `-=${windowSize.height * 1.25}`,
    // }, scrollingDuration * 2)
    timeline.to(heroTrashRef.current, {
      top: `-=${windowSize.height * 1.5}`,
    }, scrollingDuration * 2)
    timeline.to(trashMountRef.current.querySelector('.trash-mount'), {
      opacity: 1,
    }, scrollingDuration * 2)

    timeline.to(document.querySelector('.home-bg'), {
      backgroundColor: 'white',
    }, scrollingDuration * 2)

    timeline.to(trashMountRef.current, {
      scale: isMobile ? 1 : 1.75,
      x: isMobile ? `-=${trashWidth * 0.05}` : '+=0',
      y: isMobile ? '-78%' : '-130%',
    }, scrollingDuration * 2)

    timeline.to(trashMountRef.current, {
      y: isMobile ? '-100%' : '-180%',
    }, scrollingDuration * 3)

    timeline.to(document.querySelector('.home-bg'), {
      backgroundColor: theme.colors.colors.yellow,
    }, scrollingDuration * 3)

    pages.forEach((p, i) => {
      if (i) {
        timeline.to(pageRefs[i - 1].current.querySelector('.scroll-btn'), {
          opacity: 0,
          duration: 0.25 * scrollingDuration,
        }, (i - 0.25) * scrollingDuration)
      }
    })
    timeline.pause()
    timeline2.pause()

  }, [windowSize, containerWidth])

  return (
    <Box className="home-bg" bg="colors.yellow">
      <ReactFullpage
        licenseKey={process.env.REACT_APP_FULLPAGE_JS_KEY}
        scrollingSpeed={scrollingDuration * 1000}
        onLeave={(origin, destination) => {
          if (destination.isLast) {
            setTimeout(() => history.push('/catalogue'), scrollingDuration * 500)
          }
          timeline.tweenTo(destination.index * scrollingDuration)
          if (destination.index === 2) {
            timeline2.play()
          } else {
            timeline2.reverse()
          }
        }}
        render={({ fullpageApi }) => {
          return (
            <ReactFullpage.Wrapper>
              {pages.map((page, i) => (
                <div className="section" key={i} ref={pageRefs[i]}>
                  {page}
                </div>
              ))}
            </ReactFullpage.Wrapper>
          )
        }}
      />
      <Box.Fixed left="0" top="0" right="0" ref={trashMountRef} transformOrigin="50% 25%" pointerEvents="none">
        <Box ml={`${trashMx - windowSize.width * 0.04}px`} mr={`${trashMx + windowSize.width * 0.04}px`} mt={`${trashMt}px`} className="margin-adj">
          <Box.Relative>
            <BackgroundImage src={mountTop} ratio={mountRatio} className="trash-mount" />
            {!isMobile && (
              <Box.Absolute width="12%" left={responsive('25%', '31%')} top="39%">
                <Box.Relative transform={responsive('rotate(-25deg)', 'rotate(30deg)')}>
                  <Image src={trash1} />
                  <Face id={1} />
                </Box.Relative>
                <Box.Absolute
                  top="17%"
                  left="-62%"
                  width="80%"
                  opacity="0"
                  transform="scale(0)"
                  className="trash-bubble"
                  transformOrigin="100% 75%"
                >
                  <Image src={bubble2} />
                  <Box.Absolute top="33%" left="15%" right="10%" fontWeight="900" pointerEvents="all">
                    我也被丟錯！
                  </Box.Absolute>
                </Box.Absolute>
              </Box.Absolute>
            )}
            <Box.Absolute width={responsive('10%', '9%')} left={responsive('32%', '40%')} top={responsive('39%', '43%')}>
              <Box.Relative transform={responsive('rotate(-40deg)', 'rotate(0deg)')}>
                <Image src={trash2} />
                <Face id={2} />
              </Box.Relative>
              <Box.Absolute
                top={responsive('0', '-25%')}
                left={responsive('-30%', '45%')}
                width="55%"
                opacity="0"
                transform="scale(0)"
                className="trash-bubble"
                transformOrigin={responsive('100% 100%', '0% 100%')}
              >
                <Box transform={responsive('scale(-1, 1)', 'scale(1)')}>
                  <Image src={bubble3} />
                </Box>
                <Box.Absolute
                  top="22%"
                  left={responsive('16%', '8%')}
                  right="0"
                  fontWeight="900"
                  fontSize={responsive('2.875em', '0.625em')}
                  pointerEvents="all"
                >
                  走錯棚了啦！
                </Box.Absolute>
              </Box.Absolute>
            </Box.Absolute>
            <Box.Absolute width="10%" left={responsive('27%', '43%')} top={responsive('54%', '56%')}>
              <Box.Relative transform="rotate(60deg)">
                <Image src={trash3} />
                <Face id={2} />
              </Box.Relative>
              <Box.Absolute
                left={responsive('75%', '-30%')}
                top="-20%"
                width={responsive('66%', '70%')}
                opacity="0"
                transform="scale(0)"
                className="trash-bubble"
                transformOrigin={responsive('10% 100%', '90% 100%')}
              >
                <Box transform={responsive('scale(-1, 1)', 'scale(1)')}>
                  <Image src={bubble4} />
                </Box>
                <Box.Absolute top="18%" left="14%" right="5%" fontWeight="900" fontSize={responsive('3.5em', '1em')} pointerEvents="all">
                  人家明明<br />
                  可以被回收！
                </Box.Absolute>
              </Box.Absolute>
            </Box.Absolute>
            <Box.Absolute width="10%" left={responsive('34%', '33%')} top={responsive('65%', '55%')}>
              <Box.Relative transform="rotate(-10deg)">
                <Image src={trash4} />
                <Face id={1} />
              </Box.Relative>
              <Box.Absolute
                top="32%"
                left={responsive('-55%', '-70%')}
                width={responsive('80%', '100%')}
                opacity="0"
                transform="scale(0)"
                className="trash-bubble"
                transformOrigin="100% 25%"
              >
                <Box transform={responsive('rotate(5deg)', 'rotate(0deg)')}>
                  <Image src={bubble5} />
                </Box>
                <Box.Absolute top="31%" left="12%" right="12%" fontWeight="900" fontSize={responsive('3em', '1em')} pointerEvents="all">
                  要被燒掉了嗚嗚
                </Box.Absolute>
              </Box.Absolute>
            </Box.Absolute>

          </Box.Relative>
          <Box mt="-5vw"><Image src={mountMiddle} /></Box>
          <Box mt="-7vw"><Image src={mountBottom} /></Box>
        </Box>
      </Box.Fixed>

      <FullpageHeight position="fixed" left="0" right="0" pointerEvents="none">
        <Box.Absolute right={trashWidth * 0.66} top="-100%" width={trashWidth * 0.3} ref={heroTrashRef} id="hero-trash">
          <BackgroundImage src={get(data, [1, 'img'])} ratio={850 / 624} transform="rotate(7deg)" className="trash">
            <Face id="1" />
          </BackgroundImage>
          <Box.Absolute
            width={responsive('45%', '90%')}
            left={responsive('4%', '-50%')}
            top={responsive('-23%', '-20%')}
            ref={bubbleRef}
            opacity="0"
          >
            <Image src={isMobile ? bubble4 : bubble1} />
            <Box.Absolute top={responsive('28%', '38%')} left="14%" right="7%" fontWeight="900" fontSize={responsive('5.75em', '2.25em')} pointerEvents="all">
              啊..我被丟錯了
            </Box.Absolute>
          </Box.Absolute>
        </Box.Absolute>
        {/* <Box.Relative>
          <Box.Absolute top="2em" left="0" right="0">
            <BackgroundImage src={titleOverlay} ratio={titleRatio} />
          </Box.Absolute>
        </Box.Relative> */}
      </FullpageHeight>
    </Box>
  )
}

export default HomePage
