import React, { useCallback, useEffect, useRef, useState } from 'react'
import { AspectRatio } from '@chakra-ui/react';
import { interval } from 'd3-timer'

import Box from '../../components/Box';
import Link from '../../components/Link';
import Text from '../../components/Text';
import Flex from '../../components/Flex';

import Face from '../Face'
const colorsCfg = {
  A: 'green',
  B: 'orange',
  C: 'pink',
}
const rate = 0.15
const PerTrash = ({ data }) => {
  const [showFace, setShowFace] = useState(() => Math.random() < rate)
  const timer = useRef()
  const tick = useCallback(() => {
    setShowFace(Math.random() < rate)
  }, [])
  useEffect(() => {
    timer.current = interval(tick, 3000)
    return () => {
      if (timer.current) {
        timer.current.stop()
      }
    }
  }, [])
  return (
    <Link to={`/trash/${data.id}`}>
      <AspectRatio ratio={1}>
        <Box p="0.25em" bg="white">
          <Flex
            width="100%"
            bg="gray.200"
            // bg={`colors.${colorsCfg[data.recycleValue]}`}
            height="100%"
            textAlign="center"
            flexDirection="column"
            transition="all 0.25s"
            _hover={{
              boxShadow: '4px 4px 0px rgba(0,0,0,0.2)',
              transform: 'translate(-4px, -4px)',
            }}
          >
            <Box
              flex="1"
              bgImage={`url("${data.img}")`}
              backgroundSize="contain"
              backgroundRepeat="no-repeat"
              backgroundPosition="50% 50%"
              width="100%"
              position="relative"
            >
              {showFace && <Face id={data.transform.faceNo} transform={data.transform.face} />}
            </Box>
            <Box pb="0.75em">
              <Text
                // color="white"
                fontWeight="700"
                letterSpacing="0.125em"
              >{data.name}</Text>
            </Box>
          </Flex>
        </Box>
      </AspectRatio>
    </Link>
  )
}

export default PerTrash
