import React from 'react'
import Select from 'react-select'
import { MdSearch } from 'react-icons/md'
import { InputGroup, InputRightElement } from '@chakra-ui/react';

import Flex from '../../components/Flex';
import Box from '../../components/Box';
import Input from '../../components/Input';
import { responsive } from '../../components/ThemeProvider/theme';

const filterOptions = [
  {
    name: 'places',
    label: '常見地點',
    options: [
      { label: '餐廳 / 夜市', value: 1 },
      { label: '學校', value: 2 },
      { label: '家庭 - 廚房', value: 3 },
      { label: '家庭 - 衛浴', value: 4 },
      { label: '家庭 - 居家', value: 5 },
      { label: '辦公室', value: 6 },
    ],
  },
  {
    name: 'recycleStatus',
    label: '回收狀況',
    options: [
      { label: '不可回收', value: '不可回收' },
      { label: '部分可回收', value: '部分可回收' },
      { label: '可回收', value: '可回收' },
      { label: '其他', value: '其他' },
    ],
  },
  {
    name: 'recycleValue',
    label: '回收價值',
    options: [
      { label: '低', value: 'C' },
      { label: '中', value: 'B' },
      { label: '高', value: 'A' },
    ],
  },
]

const valueToOption = filterOptions.reduce((vto, field) => {
  vto[field.name] = field.options.reduce((vt, opt) => {
    vt[opt.value] = opt
    return vt
  }, {})
  return vto
}, {})

const FilterAndSearch = ({ onChange, values, setFieldValue }) => {
  return (
    <Flex py="0.5em">
      {filterOptions.map(({ name, label, options }) => (
        <Box key={name} width="15rem" px="1em" fontSize="0.75em">
          <Select
            options={options}
            placeholder={label}
            value={valueToOption[name][values[name]]}
            onChange={selected => setFieldValue(name, selected && selected.value)}
            name={name}
            isClearable
            styles={{
              valueContainer: provided => ({
                ...provided,
                padding: '0.125em 0.5em',
              }),
              placeholder: provided => ({
                ...provided,
                marginLeft: '0.125em',
                marginRight: '0.125em',
              }),
              indicatorSeparator: () => ({
                display: 'none',
              }),
              control: provided => ({
                ...provided,
                minHeight: '3em',
                borderRadius: 0,
                border: 'none',
                borderBottom: '0.175em solid black'
              }),
            }}
          />
        </Box>
      ))}
      <InputGroup ml="1em" width={responsive('', '15em')} flex={responsive('1', 'auto')} fontSize={responsive('2.5em', '1em')}>
        <Input
          lineHeight="2.75"
          fontSize="0.75em"
          placeholder="輸入垃圾關鍵字"
          value={values.search}
          name="search"
          onChange={onChange}
          borderRadius="0"
          border="none"
          borderBottom="0.175em solid black"
          _hover={{
            borderBottomColor: "black"
          }}
          _focus={{
            borderBottomColor: "black"
          }}
          _active={{
            borderBottomColor: "black"
          }}
        />
        <InputRightElement width="2.5em" height="2.5em" fontSize="1em" px="0" children={<MdSearch size="1.5em" />} />
      </InputGroup>
    </Flex>
  )
}

export default FilterAndSearch
