import React from 'react'

import rc from './rc.svg'
import citi from './citi.svg'
import foodpanda from './foodpanda.svg'
import Box from '../Box'
import Flex from '../Flex'
import Image from '../Image'
import { responsive } from '../ThemeProvider/theme'

const sponsor = [
  citi,
  rc,
  foodpanda
]

const Sponsor = ({ bg, textColor, bgColor, px, logoProps, ...props }) => {
  return (
    <Box position="relative" py="3.125em" bg={bg} {...props}>
      <Box
        position="absolute"
        top="0"
        left="0"
        right="0"
        bottom="0"
        bg={bgColor}
        opacity="0.2"
      />
      <Box
        position="relative"
        fontSize={responsive("2.5em", '2em')}
        color={textColor || 'colors.yellow'}
        px={px || responsive('3rem', '4.6875rem')}>
        贊助單位
      </Box>
      <Flex justifyContent="center">
        {sponsor.map((logo, k) => (
          <Box px="1.25em" key={k} {...logoProps}>
            <Image src={logo} />
          </Box>
        ))}
      </Flex>
    </Box>
  )
}

export default Sponsor
